/*
	该scss文件中主要用于清除默认样式，指定基础样式，例如font-family、背景之类的
*/
body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-size: 0;
  font-family: 'Microsoft YaHei', 'Avenir', Helvetica, Arial, sans-serif; }
article,
section,
div,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
li {
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-family: 'Microsoft YaHei', 'Avenir', Helvetica, Arial, sans-serif; }
dl,
dt,
span,
samp,
strong {
  font-size: 0;
  font-weight: normal;
  font-family: 'Microsoft YaHei', 'Avenir', Helvetica, Arial, sans-serif; }
h1,
h2,
h3,
h4,
h5,
h6 {
  display: block;
  font-weight: normal; }
ul li {
  display: inline-block;
  list-style-type: none; }
